<template>
  <div class="edit">
    <div class="field" @mousemove="mousemove">
      <div v-for="cat in categolies" @contextmenu.self="cat.remove" @mousedown="cat.mousedown" @mouseup="cat.mouseup" :key="cat.id" :style="cat.style" class="categolies card">
        <div class="card-header">
          <p @contextmenu.self="cat.remove" @click="cat.edit" v-if="!cat.editMode" >{{cat.title}}</p>
          <textarea @keypress="cat.keypress" v-if="cat.editMode" v-model="cat.title"></textarea>
        </div>
        <div class="card-body">
          <div v-for="item in cat.items" :key="item.id" :style="item.style" @contextmenu="item.pop()" class="item card">
            <div class="card-body">
              <h6 class="card-title">{{item.title}}</h6>
              <p @click="item.edit()" v-if="!item.editMode">{{item.answer}}</p>
              <textarea @keypress="item.keypress" v-if="item.editMode" v-model="item.answer"></textarea>
            </div>
          </div>
        </div>
      </div>
      <div v-for="item in items" @contextmenu="item.remove" @mousedown="item.mousedown" @mouseup="item.mouseup" :key="item.id" :style="item.style" class="item card">
        <div class="card-body">
          <h5 class="card-title">{{item.title}}</h5>
          <p class="card-text" @click="item.edit" v-if="!item.editMode">{{item.answer}}</p>
          <textarea @keypress="item.keypress" v-if="item.editMode" v-model="item.answer"></textarea>
       </div>
      </div>
  </div>
  <div class="tools">
    <input type="button" value="質問作成" @click="addq">
    <label for="getfile">質問インポート</label>
    <input type="file" id="getfile" accept="text/*" @change="importQ">
    <input type="button" value="セーブ" id="savefile" accept="text/*" @click="exp">
    <label for="getfile">ロード</label>
    <input type="file" id="getfile" accept="text/*" @change="load">

  </div>
  </div>
</template>

<script>
let zi = 0

export default{
  data () {
    return {
      items: [
      ],
      categolies: []
    }
  },
  mounted () {
  },
  methods: {
    importQ: function (e) {
      const reader = new FileReader()
      reader.readAsText(e.target.files[0])
      reader.addEventListener('load', _ => {
        reader.result.split('\n').forEach(q => {
          this.items.push(this.createData(q))
        })
      })
    },
    load: function (e) {
      const reader = new FileReader()
      reader.readAsText(e.target.files[0])
      reader.addEventListener('load', _ => {
        const data = JSON.parse(reader.result)
        data.items.forEach(item => {
          this.items.push(
            this.createData(item.title, item.answer, item.pos.x, item.pos.y)
          )
        })

        data.categolies.forEach(add => {
          const cat = this.createCategory(add.pos.x, add.pos.y, add.title)
          add.items.forEach(item => {
            cat.items.push(
              this.createData(item.title, item.answer, item.pos.x, item.pos.y, cat)
            )
          })
          this.categolies.push(cat)
        })

        console.log(data)
      })
    },
    exp: function () {
      const exp = {
        items: this.items,
        categolies: this.categolies
      }
      const fss = require('fast-safe-stringify')
      const strExp = fss(exp)
      let blob = new Blob([strExp], {type: 'text/plan'})
      let link = document.createElement('a')
      link.href = URL.createObjectURL(blob)
      link.download = '作ったファイル.txt'
      link.click()
    },
    mousemove: function () {},
    createCategory (x, y, title) {
      const self = this
      const add =
        {
          title: title,
          id: Math.floor(Math.random() * 10000),
          pos: {
            x: x,
            y: y
          },
          items: [

          ],
          move: false,
          style: {
            top: '0px',
            left: '0px',
            zIndex: 1
          },
          update: function () {
            this.style.top = this.pos.y + 'px'
            this.style.left = this.pos.x + 'px'
          },
          test: function () {
            this.style.pos += 50
            add.update()
          },
          mousedown: function (e) {
            zi += 1
            add.style.zIndex = zi
            const diff = {
              x: e.pageX - add.pos.x,
              y: e.pageY - add.pos.y
            }
            add.move = true
            self.mousemove = (me) => {
              if (add.move) {
                add.pos.x = me.pageX - diff.x
                add.pos.y = me.pageY - diff.y
                add.update()
              }
            }
          },
          mouseup: function () {
            add.move = false
            self.mousemove = () => {}
          },
          keypress: function (e) {
            const key = e.keyCode || e.charCode || 0
            // 13はEnterキーのキーコード
            if (key === 13) {
              add.editMode = false
            }
          },
          edit: function () {
            add.editMode = true
          },
          editMode: false,
          add: function (item) {
            item.parentCategory = add
            add.items.push(item)
          },
          remove: function () {
            if (confirm('Are you sure?')) {
              self.categolies = self.categolies.filter(cat => {
                if (cat.id !== add.id) return cat
              })
            }
          }
        }
      add.update()
      return add
    },
    createData (title, answer = '未回答', x = 0, y = 0, parent) {
      const self = this
      const add = {
        title: title,
        answer: answer,
        parentCategory: parent,
        id: Math.floor(Math.random() * 10000),
        pos: {
          x: x,
          y: y
        },
        style: {
          top: 0 + 'px',
          left: 0 + 'px',
          zIndex: 1
        },
        editMode: false,
        update: function () {
          this.style.top = this.pos.y + 'px'
          this.style.left = this.pos.x + 'px'
        },
        mousedown: function (e) {
          zi += 1
          add.style.zIndex = zi
          const diff = {
            x: e.pageX - add.pos.x,
            y: e.pageY - add.pos.y
          }
          add.move = true
          let pastDup
          self.mousemove = (me) => {
            if (add.move) {
              add.pos.x = me.pageX - diff.x
              add.pos.y = me.pageY - diff.y
              add.update()
              const dups = self.items.filter(ele => {
                if (Math.abs(ele.pos.x + 150 - me.pageX) < 150 && ele !== add) {
                  if (Math.abs(ele.pos.y + 50 - me.pageY) < 50) {
                    return ele
                  }
                }
              })
              Object.keys(self.categolies).forEach(ele => {
                ele = self.categolies[ele]
                if (Math.abs(ele.pos.x + 170 - me.pageX) < 170 && ele !== add) {
                  if (Math.abs(ele.pos.y + 50 - me.pageY) < 50) {
                    dups.push(ele)
                  }
                }
              })
              if (pastDup !== void 0) {
                if (Array.isArray(pastDup.items)) {
                  pastDup.style.backgroundColor = 'white'
                } else {
                  pastDup.style.backgroundColor = ''
                }
                add.dup = false
                add.dupTo = null
              }
              if (dups.length > 0) {
                dups[0].style.backgroundColor = 'red'
                add.dup = true
                add.dupTo = dups[0]
                pastDup = dups[0]
              }
            }
          }
        },
        mouseup: function () {
          add.move = false
          if (add.dup) {
            add.dupTo.add(add)
            self.items = self.items.filter(item => {
              if (item.id !== add.id) return item
            })
            if (Array.isArray(add.dupTo.items)) {
              add.dupTo.style.backgroundColor = 'white'
            } else {
              add.dupTo.style.backgroundColor = 'rgb(245, 245, 220)'
            }
            add.dup = null
          }
          self.mousemove = () => {}
        },
        keypress: function (e) {
          const key = e.keyCode || e.charCode || 0
          // 13はEnterキーのキーコード
          if (key === 13) {
            add.editMode = false
          }
        },
        edit: function () {
          add.editMode = true
        },
        remove: function () {
          if (confirm('Are you sure?')) {
            self.items = self.items.filter(item => {
              if (item.id !== add.id) return item
            })
          }
        },
        pop: function () {
          if (confirm('are you sure to pop?')) {
            self.items.push(add)
            add.parentCategory.items = add.parentCategory.items.filter(item => {
              if (item.id !== add.id) return item
            })
          }
        },
        add: function (q) {
          const cat = self.createCategory(100, 100, '新規グループ')
          self.categolies.push(cat)
          cat.add(q)
          cat.add(add)

          self.items = self.items.filter(item => {
            if (item.id !== add.id && item.id !== q.id) return item
          })
        }
      }
      add.update()
      return add
    },
    addq () {
      this.items.push(
        this.createData('新規質問')
      )
    }
  }
}
</script>
<style lang="scss" scoped>
  .field{
    width: 100%;
    height:100vh;
    position: relative;
    overflow: hidden;
    background: rgb(172, 255, 133);
  }
  .item{
    width:320px;
    .card-title{
      font-size: 10px;
    }
    position: absolute;
    -ms-user-select: none; /* IE 10+ */
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    display:block;
    & p {
      border-bottom: transparent;
    }
    font-size:10px;
  }
  .categolies{
    position: absolute;
    -ms-user-select: none; /* IE 10+ */
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    background :white;
    .item{
      width:320px;
      position: static;
    }
  }
  p{
    display: block;
  }
  .tools{
    width:100%;
    position:fixed;
    bottom: 0;
    background:white;
  }
  .card-body{
    padding: 10px;
  }
</style>



